@import url(https://fonts.googleapis.com/css?family=Righteous|Lato);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin:0;
}

nav ul {
  padding:0;
  margin: 0;
  list-style: none;
}

.App {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.footer {
  margin-top: 50px;
}

.footer__list {
  display: flex;
  justify-content: space-around;
}

.nav {
  border-top: 1px solid #ccc;
}

.nav__list {
    display: flex;
    width: 100%;
    padding: 15px 0;
    justify-content: space-around;
    font-size: 0.7rem;
}

.nav__link {
    text-decoration: none;
    -webkit-transition: color 0.4s linear;
    transition: color 0.4s linear;
    color: #333;
}

.nav__link:hover {
  color: #8f0101;
}

.section__header {
  display: flex;
  justify-content: center;
}

.section__title {
  font-family: 'Righteous', cursive;
  font-size: 7vmin;
  color: #444;
}

.section__content {
  margin-top: 25px;
  padding: 0 25px;
}

.section__paragraph {
  margin-top: 15px;
}

.section__paragraph:first-child {
  margin-top: 0;
}

.section__well {
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 15px;

  margin-top: 25px;
}

.section__wellTitle {
  margin-bottom: 15px;
}

